.w100 {
  flex-basis: 100%;
  flex-grow: 1; }

.w75 {
  flex-basis: 75%; }

.w50 {
  flex-basis: 50%; }

.w25 {
  flex-basis: 25%; }

.w66 {
  flex-basis: 66.66%; }

.w33 {
  flex-basis: 33.33%; }

.flex-block {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  flex-direction: row;
  align-items: flex-start;
  margin-right: 0; }
  .flex-block > * {
    margin-right: 10px; }
    .flex-block > *:last-child {
      margin-right: 0; }
  @media only screen and (max-width: 420px) {
    .flex-block.no-flex-mobile {
      display: block; }
      .flex-block.no-flex-mobile > * {
        margin-right: 0; }
        .flex-block.no-flex-mobile > *:last-child {
          margin-right: 0; }
      .flex-block.no-flex-mobile > * {
        margin-bottom: 0; }
        .flex-block.no-flex-mobile > *:last-child {
          margin-bottom: 0; } }
  .flex-block > *:last-child {
    margin-right: 0; }
  .flex-block.no-margin > * {
    margin-right: 0; }
    .flex-block.no-margin > *:last-child {
      margin-right: 0; }
  .flex-block.center {
    align-items: center; }
  .flex-block.full-width {
    flex: 1; }
  .flex-block.space-between {
    justify-content: space-between; }
    .flex-block.space-between > div {
      margin: 0; }
  .flex-block.wrap {
    flex-wrap: wrap; }
  .flex-block.stretch {
    align-items: stretch; }
  .flex-block.fb-vtop {
    align-items: flex-start; }
  .flex-block.fb-vcenter {
    align-items: center; }
  .flex-block.fb-vbottom {
    align-items: flex-end; }
  .flex-block.pos-left {
    justify-content: flex-start; }
  .flex-block.pos-right {
    justify-content: flex-end; }
  .flex-block.pos-center {
    justify-content: center; }
  .flex-block.space-around {
    justify-content: space-around; }

.show-from-tablet {
  display: none !important; }
  @media only screen and (max-width: 992px) {
    .show-from-tablet {
      display: block !important; } }

@media only screen and (max-width: 992px) {
  .hide-after-mobile {
    display: none !important; } }

.card {
  padding: 40px 45px;
  padding-top: 10px;
  box-sizing: border-box;
  border: 1px solid #DCDFD5;
  border-radius: 5px; }
  .card > * {
    margin-bottom: 20px; }
    .card > *:last-child {
      margin-bottom: 0; }
  .card .card-table {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    flex-direction: row;
    align-items: center; }
    .card .card-table > * {
      margin-right: 10px; }
      .card .card-table > *:last-child {
        margin-right: 0; }
    .card .card-table > * {
      margin-right: 60px; }
      .card .card-table > *:last-child {
        margin-right: 0; }
    .card .card-table > div:first-child {
      color: #ABB09F; }
  .card > div:first-child {
    display: inline-block;
    padding: 4px 10px;
    border-radius: 3px;
    line-height: 1.2;
    background: #DCDFD5;
    color: #8D9084; }
  .card.active {
    background-color: #ffffff;
    box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
    border: none; }
    .card.active > div:first-child {
      background: linear-gradient(170.36deg, #038C73 46.78%, #6CC47B 97.01%);
      color: #ffffff; }
