.w100 {
  flex-basis: 100%;
  flex-grow: 1; }

.w75 {
  flex-basis: 75%; }

.w50 {
  flex-basis: 50%; }

.w25 {
  flex-basis: 25%; }

.w66 {
  flex-basis: 66.66%; }

.w33 {
  flex-basis: 33.33%; }

.flex-block {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  flex-direction: row;
  align-items: flex-start;
  margin-right: 0; }
  .flex-block > * {
    margin-right: 10px; }
    .flex-block > *:last-child {
      margin-right: 0; }
  @media only screen and (max-width: 420px) {
    .flex-block.no-flex-mobile {
      display: block; }
      .flex-block.no-flex-mobile > * {
        margin-right: 0; }
        .flex-block.no-flex-mobile > *:last-child {
          margin-right: 0; }
      .flex-block.no-flex-mobile > * {
        margin-bottom: 0; }
        .flex-block.no-flex-mobile > *:last-child {
          margin-bottom: 0; } }
  .flex-block > *:last-child {
    margin-right: 0; }
  .flex-block.no-margin > * {
    margin-right: 0; }
    .flex-block.no-margin > *:last-child {
      margin-right: 0; }
  .flex-block.center {
    align-items: center; }
  .flex-block.full-width {
    flex: 1; }
  .flex-block.space-between {
    justify-content: space-between; }
    .flex-block.space-between > div {
      margin: 0; }
  .flex-block.wrap {
    flex-wrap: wrap; }
  .flex-block.stretch {
    align-items: stretch; }
  .flex-block.fb-vtop {
    align-items: flex-start; }
  .flex-block.fb-vcenter {
    align-items: center; }
  .flex-block.fb-vbottom {
    align-items: flex-end; }
  .flex-block.pos-left {
    justify-content: flex-start; }
  .flex-block.pos-right {
    justify-content: flex-end; }
  .flex-block.pos-center {
    justify-content: center; }
  .flex-block.space-around {
    justify-content: space-around; }

.show-from-tablet {
  display: none !important; }
  @media only screen and (max-width: 992px) {
    .show-from-tablet {
      display: block !important; } }

@media only screen and (max-width: 992px) {
  .hide-after-mobile {
    display: none !important; } }

.header {
  border-bottom: 1px solid #E6E8E0; }
  .header > div {
    height: 100px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    flex-direction: row;
    align-items: center; }
    .header > div > * {
      margin-right: 10px; }
      .header > div > *:last-child {
        margin-right: 0; }
    .header > div .phone > div:first-child {
      line-height: 26px; }
    .header > div .logo {
      position: relative;
      width: 157px;
      height: 26px; }
      .header > div .logo:before {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        content: '';
        background: url(/static/img/skb_logo.svg) center center no-repeat;
        background-size: contain; }
